@import '../../../base';
@import '../forms';

.persons-container {
  @extend %input-container;
  display: flex;
  align-items: center;
  height: $input-height;

  .img-container {
    @extend %img-container;
  }

  .text-input {
    @extend %input-field;
    transform: none;
    display: flex;
    align-items: center;
    cursor: pointer;

    p {
      margin: 0;
    }
  }

  .drop-arrow {
    @extend %drop-arrow-container;
  }

  .drop-container {
    position: absolute;
    top: 120%;
    background: white;
    padding: 18px;

    @extend %input-container-focus;
    border-radius: $input-border-radius;

    &:after {
      content: '';
      display: block;
      position: absolute;
      z-index: 0;
      top: -7px;
      left: calc(20% - 6px);
      margin: 0 0 0 -6px;
      width: 12px;
      height: 12px;
      transform: rotate(45deg);
      border-radius: 4px 0 0 0;
      background: white;
      border-left: 2px solid $color-hover;
      border-top: 2px solid $color-hover;
    }

    .value-container {
      display: flex;
      margin-bottom: 30px;

      &:nth-last-of-type(1) {
        margin-bottom: 0;
      }

      p {
        flex: 1;
        padding: 0 32px;
        margin: 0;
      }
    }

    .count {
      font-weight: $font-weight-semibold;
      font-size: $font-size-small;

      color: $color-hover;
      transform: translateY(5px);
    }

    .desc {
      font-weight: $font-weight-bold;
      font-size: $font-size-tiny;
      color: $color-title;

      margin-left: 5px;
      margin-top: -5px;
    }

    .plus,
    .minus {
      font-size: $font-size-tiny;
      transition: transform 0.3s ease;
      &:active {
        transform: scale(1.5);
      }
    }

    .plus {
      color: $color-hover;
    }

    .minus {
      color: $color-other-desc2;
    }
  }

  .drop-container-closed {
    animation: animate-z-invisible 0.3s linear, animate-invisible 0.3s ease-in-out;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }

  .drop-container-closed-init {
    @extend %close-init;
  }
  
  .drop-container-opened {
    animation: animate-z-visible 0.3s linear, animate-visible 0.3s ease-in-out;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }
}