@import '../../../base';

.detail-main-container-new {
  %total-text {
    font-weight: $font-weight-bold;
    font-size: $font-size-tiny;
    color: $color-input-text;
    margin: auto;
  }

  %cost-text {
    font-weight: $font-weight-heavy;
    font-size: $font-size-tiny;
    text-align: right;
    color: $color-input-text;
    flex: 1;
  }

  max-width: 1108px;
  margin: auto;
  padding: 0 15px;
  margin-top: 90px;
  margin-bottom: 250px;

  @media screen and (max-width: 991px) {
    margin-top: 30px;
  }

  .detail-left-container {
    margin-right: 30px;
    max-width: 780px;
    min-height: 500px;
    background-color: white;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    padding: 30px;

    @media screen and (max-width: 991px) {
      max-width: none;
      padding: 15px 0;
      margin-right: 0;
      box-shadow: none;
    }
  }

  .bottom-container {
    position: fixed;
    z-index: 1000;
    left: 0;
    bottom: 0;
    width: 100vw;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 -5px 6px 0 rgba(70,115,122,.1);
    height: 80px;

    .bottom-inner-container {
      display: flex;
      align-items: center;
      width: 100%;
      padding: 10px 20px;

      @media screen and (min-width: 400px) {
        padding: 10px 40px;
      }

      .price-container {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
  
        h5 {
          font-weight: $font-weight-bold;
          font-size: $font-size-small;
          color: $color-input-form-landing-desc2;
        }
  
        h6 {
          font-weight: $font-weight-normal;
          font-size: $font-size-tiny;
  
          color: $color-input-form-landing-desc2;
        }
  
        h4 {
          font-weight: $font-weight-heavy;
          font-size: $font-size-big;
  
          color: $color-input-text;
          display: flex;
          align-items: center;
  
          .price {
            margin-left: 5px;
          }
        }
  
        h4,
        h5,
        h6 {
          margin: 0;
        }

        .price {
          float: right;
          font-weight: $font-weight-bold;
          font-size: $font-size-tiny;
          text-decoration-line: line-through;
          color: $color-input-form-landing-desc2;
        }
      }

      .resp-button-container {
        .sign-up {
          margin-bottom: 5px;
        }

        @media screen and (min-width: 400px) {
          display: flex;

          .sign-up {
            margin-left: 20px;
            margin-right: 10px;
            margin-bottom: 0;
            display: flex;
            align-items: center;

            button {
              height: 100%;
            }
          }
        }

        &.small {
          button {
            min-height: 32px !important;

            @media screen and (min-width: 400px) {
              min-height: 48px !important;
            }
          }
        }

        &.big {
          button {
            min-height: 48px !important;
          }
        }
  
        button {
          width: 100px !important;
          min-width: 0 !important;

          p {
            font-size: $font-size-tiny;
          }
        }
      }
    }
  }

  .upper-right-container {
    padding: 30px;
    // max-width: 196px;
    // max-height: 450px;
    background: #FFFFFF;
    /* Mild shadow */
    box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);

    .logo-container {
      border-bottom: solid 1px $color-line;
      display: flex;
      flex-wrap: wrap;
      padding-bottom: 35px;

      .logo-inner-container {
        min-height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;
        .company-logo {
          max-height: 60px;
          max-width: 60px;
        }
      }
      
      .plan-name {
        color: $color-input-text;
        font-size: $font-size-tiny + $font-size-extra-tiny;
        font-weight: $font-weight-bold;
        margin: auto 0;
        word-break: break-all;
        flex: 1;
      }

      .edit-detail-value-container-new {
        margin-top: 20px;
        width: 90%;
        display: flex;
        flex-wrap: wrap;

        .edit-detail-value {
          width: 50%;
        }
      }

      .sign-up {
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    .submit-container {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end;
  
      .sign-up-container {
        margin: 19px 0;
        max-width: 180px;
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
  
        .cost-container {
          width: 100%;
          display: flex;
          flex-wrap: wrap;
  
          p {
            width: 100%;
            display: flex;
            align-items: center;
          }
        }
        .gst-text {
          font-weight: $font-weight-normal;
          font-size: $font-size-tiny;
          color: $color-other-desc;
          transform: translateY(-8px);
        }
  
        .total-text {
          @extend %total-text;
        }
  
        .cost-text,
        .final-cost-text {
          @extend %cost-text;
        }
  
        .final-cost-text {
          font-size: $font-size-big;
        }
      }
    }

    .divider {
      border-top: 1px solid $color-input-outline;
      width: 100%;
    }
  }

  .error-string {
    margin-left: 7px;
    font-size: $font-size-small;
    font-weight: $font-weight-semibold;
    color: $color-error-alt;

    @media screen and (max-width: 991px) {
      font-size: $font-size-smallest;
    }

    .error-sub {
      display: block;
    }
  }

  .piggy {
    position: absolute;
    right: 0;
    left: 0;
    transform: translateY(-120%);

    @media screen and (max-width: 991px) {
      position: relative;
      transform: none;
      padding: 0 5px;
      transform: none;
      margin-bottom: 10px;

      .piggy-desc-container {
        text-align: center;
      }
    }
  }
}
