@import '../../../base';

%piggy-hover-style {
  background-color: $color-input-form-landing-desc;

  .piggy-right-container {
    .piggy-desc-container {
      .main-text {
        color: white;
      }
    
      .desc-text {
        color: white;
      }
    }
  }

  .piggy-img-container {
    filter: brightness(1000) saturate(0);
  }
}

.piggy-container-new {
  border: solid 1px $color-input-form-landing-desc;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px !important;

  transition: background-color 0.3s ease;

  &.piggy-hover {
    cursor: pointer;
    &:hover {
      @extend %piggy-hover-style;
    }
  }

  &.piggy-hovered {
    @extend %piggy-hover-style;
  }

  // @media screen and (max-width: 568px) {
  //   flex-wrap: wrap;
  //   padding-top: 12px !important;
  //   padding-bottom: 20px !important;

  //   .piggy-img-container {
  //     flex: 0 0 50%;
  //     justify-content: center;
  //     align-items: center;
  //     display: flex;
  //   }
  // }

  .wrap-container {
    flex-wrap: wrap;
    padding: 0;
    align-items: center;
  }

  .piggy-right-container {
    padding-left: 5px;
    flex: 1;
    // @media screen and (max-width: 568px) {
    //   padding-left: 0;
    //   flex: 0 0 50%;
    // }

    .piggy-desc-container {
      min-height: 48px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      .main-text {
        margin-top: 10px;
        font-size: $font-size-big;
        font-weight: $font-weight-heavy;
        color: $color-input-form-landing-desc;
        line-height: 15px;
        transition: color 0.3s ease;

        @media screen and (max-width: 468px) {
          font-size: $font-size-small;
        }

        img {
          margin: 0 5px;
          max-width: 26px;
          max-height: 26px;
        }
      }
    
      .desc-text {
        font-size: $font-size-extra-tiny;
        font-weight: $font-weight-semibold;
        color: $color-input-form-landing-desc;
        line-height: normal;
        transition: color 0.3s ease;

        @media screen and (max-width: 468px) {
          font-size: $font-size-extra-tiny;
        }
      }

      .desc-padding {
        margin-top: -5px;
        margin-bottom: 0;
      }

      @media screen and (max-width: 568px) {
        .main-text {
          line-height: 18px;
        }
      }
    }
  }
}