@import '../../../base';

.single-check-box-container {
  position: relative;
  z-index: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  border: 1px solid $color-input-outline;
  box-sizing: border-box;
  border-radius: 4px;

  padding: 7px 10px 7px 10px;
  transition: border 0.3s ease;

  @media screen and (max-width: 768px) {
    padding: 6px;
  }

  .bg {
    z-index: -1;
    position: absolute;
    border-radius: 4px;
    top:0;
    left:0;
    height: 100%;
    width: 100%;
    transition: background-color 0.3s ease, transform 0.3s ease;
    transform-origin: left;
  }

  .box {
    margin-right: 6px;
    width: 14px;
    height: 14px;
    background-color: white;
    box-sizing: border-box;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: border 0.3s ease;

    img {
      transition: transform 0.3s ease;
    }
  }

  .desc {
    font-weight: $font-weight-semibold;
    font-size: $font-size-tiny;
    text-align: center;
    margin: 0;

    transition: color 0.5s ease;

    @media screen and (max-width: 768px) {
      font-size: $font-size-small * 0.5;
    }
  }
}

.single-check-box-checked {
  border: 1px solid white;

  .bg {
    background-color: $color-input-form-landing-desc;
    transform: scaleX(1);
  }
  
  .box {
    border: none;
    img {
      transform: scale(1);
    }
  }

  .desc {
    color: white;
  }
}

.single-check-box-non-checked {
  border: 1px solid $color-input-outline;

  .bg {
    background-color: white;
    transform: scaleX(0);
  }

  .box {
    border: 1px solid $color-input-outline;
    img {
      transform: scale(0);
    }
  }

  .desc {
    color: $color-input-form-landing-desc2;
  }
}