@import '../../../base';

.image-selection-margin {
  max-width: 960px;
  margin: auto;
  margin-bottom: 40px;
}

.image-selection-container {
  @media screen and (max-width: 575px) {
    margin-bottom: 30px;
  }


  .title-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
    @media screen and (max-width: 575px) {
      margin-bottom: 10px;
    }
  

    h3 {
      font-weight: $font-weight-bold;
      font-size: $font-size-big;
      text-align: center;

      color: $color-hover;
  
      @media screen and (max-width: 575px) {
        font-size: $font-size-smallest;
      }
    }

    .tooltip-container {
      transform: translateX(25px);
      @media screen and (max-width: 575px) {
        transform: translateX(10px) translateY(-2px);
      }
    }
  }

  .options-container {
    max-width: 660px;
    margin: auto;
  }

  .item-container-wrapper {
    &:nth-of-type(even) {
      .item-container-wrapper-inner,{
        padding-left: 10px;
      }

      .item-container,
      .item-container-bottom {
        margin-left: 0;
      }
    }

    &:nth-of-type(odd) {
      .item-container-wrapper-inner {
        padding-right: 10px;
      }
      
      .item-container,
      .item-container-bottom {
        margin-right: 0;
      }
    }
  }

  .item-container-wrapper-inner {
    width: 100%;
    height: 100%;
    padding: 10px 0;

  }

  .item-container {
    margin: auto;
    background: white;
    border: 1px solid $color-input-outline;
    // border-bottom: none;
    box-sizing: border-box;
    border-radius: 12px;
    // border-bottom-right-radius: 0;
    // border-bottom-left-radius: 0;
    width: 230px;
    height: 200px;
    max-width: 100%;
    max-height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;

    @media screen and (max-width: 575px) {
      width: 100%;
      // height: 100%;
    }

    &:hover {
      transform: scale(1);
    }

    &:active {
      transform: scale(1);
    }

    &.selected {
      border: 1px solid $color-hover;
      // border-bottom: none;
    }

    &.click-left {
      .bg {
        transform-origin: left;
      }

      .bg-active {
        transform-origin: right;
      }
    }

    &.click-right {
      .bg {
        transform-origin: right;
      }

      .bg-active {
        transform-origin: left;
      }
    }
  
    .bg {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 12px;
      // border-bottom-right-radius: 0;
      // border-bottom-left-radius: 0;
      background-color: $color-hover-alt3;
      transform: scaleX(0);
      transition: background-color 0.3s, transform 0.3s ease;
    }

    .bg-active {
      transform: scaleX(1);
      border: 1px solid $color-hover;
      // border-bottom: none;
    }

    .image {
      position: relative;
      z-index: 1;
      height: inherit;
      // max-height: 173px;
      width: 100%;
      @-moz-document url-prefix() {
        width: 80%;
      }
      display: flex;
      align-items: center;
      justify-content: center;

      @media screen and (max-width: 575px) {
      }

      img {
        width: 100%;
        @-moz-document url-prefix() {
          max-width: 150px;
        }
        height: auto;
      }
    }
  }

  .item-container-bottom {
    position: relative;
    width: 230px;
    max-width: 100%;
    margin: auto;
    background-color: white;

    border: 1px solid $color-input-outline;
    box-sizing: border-box;
    border-radius: 12px;
    // border-top-right-radius: 0;
    // border-top-left-radius: 0;

    .desc {
      position: relative;
      z-index: 1;
      font-weight: $font-weight-bold;
      font-size: $font-size-smallest;
      text-align: center;
      min-height: 75px;
      max-height: 100%;

      color: $color-input-text;
      padding: 0 10px;

      display: flex;
      flex-direction: column;
      justify-content: center;

      @media screen and (max-width: 575px) {
        font-size: $font-size-extra-tiny;
        padding: 0 8px 10px 8px;
      }

      &.desc-padding {
        padding-top: 25px;

        justify-content: flex-start;
      }

      p {
        margin: 0;
      }

      .sub-title {
        font-weight: $font-weight-normal;
        font-size: $font-size-smallest;
        text-align: center;
        color: $color-input-form-landing-desc2;

        @media screen and (max-width: 575px) {
          font-size: $font-size-extra-tiny;
          padding: 0 8px;
        }
      }
    }

    .inner-tooltip {
      position: absolute;
      right: 10px;
      top: 3px;
      z-index: 2;
    }
  }
}
