/**
 * DatePickerV2.scss
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../../base';
 @import '../../forms';

.dateinner-v3 {
  position: fixed;
  z-index: -999;
  // min-width: 256px;
  // margin: 10px 0;

  animation: animate-z-invisible 0.3s linear;
  animation-direction: normal;
  animation-fill-mode: forwards;

  &.inner-absolute {
    position: absolute;
  }

  .close-button-container {
    display: none !important;
    // @media screen and (max-width: 575px){
    //   padding-right: 20px;
    //   padding-top: 20px;
    //   display: flex !important;
    //   justify-content: flex-end;
    // }
  }

  .close-icon {
    width: 18px;
    height: 18px;
    margin: 10px;
    transform: translateX(10px) translateY(-10px);
    filter: brightness(0) saturate(100%) invert(95%) sepia(7%) saturate(540%) hue-rotate(143deg) brightness(96%) contrast(85%);

    &:hover {
      transition: transform 0.3s, filter 0.3s ease;
      transform: translateX(10px) translateY(-10px) scale(1.1);
      filter: brightness(0) saturate(100%) invert(54%) sepia(86%) saturate(1621%) hue-rotate(128deg) brightness(103%) contrast(101%);
    }
  }
  
  .date-inner-container {
    // margin: auto;
    width: 256px;
    padding: 10px;
    border-radius: 8px;
    
    background-color: white;
    
    // @media screen and (min-width: 576px){
      padding-top: 15px 20px 20px 20px;
    // }
    
    p {
      font-size: $font-size-tiny;
      font-weight: $font-weight-normal;
      color: $color-caption;
      cursor: default;
    }

    .selector-left {
        display: flex;
        padding-right: 10px;
    }
    
    .selector-right {
        display: flex;
        padding-left: 0;
    }

    .btn {
        height: 36px;
        color: #354f53 !important;
        font-weight: 300;
        border-radius: 8px;
    }

    .date-arrow-style {
      color: $color-line;
      padding: 0;
      display: flex;
      cursor: pointer;
  
      .angle-left {
        transform: translateX(0) rotate(90deg);
      }
    
      .angle-right {
        transform: translateX(5px) rotate(-90deg);
      }
    }
    
    .date-arrow-style:active {
      color: $color-hover;
    }
  
    .date-arrow-style:focus {
      outline: none;
    }
  
    .date-arrow-style:active {
      filter: $color-filter-hover-alt;
    }

    .range-left,
    .range-right {
      display: flex;
      align-items: center;

      p {
        margin: 0;
      }

      .selector-left {
        display: flex;
        justify-content: flex-end;
      }
    }

    .range-left {
      padding-left: 15px;
      .date-arrow-style {
        // transform: translateX(15px);
      }
    }
  }

  .date-container-closed {
    visibility: hidden;
    opacity: 0;

    height: 100%;
    min-height: 310px;
    
    // @media screen and (min-width: 576px){
      transition: transform 0.3s, visibility 0.3s, opacity 0.3s ease;
      animation: animate-invisible 0.3s ease-in-out;
      animation-direction: normal;
      animation-fill-mode: forwards;
      border-radius: 8px;
    // }
  }

  .date-container-opened {
    // visibility: visible;
    // opacity: 1;
    // width:100%;
    // height: 100%;
    // min-height: 310px;
    
    // @media screen and (min-width: 576px){
      transform: scale(1);
      transform-origin: top left;
      transition: transform 0.3s, visibility 0.3s, opacity 0.3s ease;
      animation: animate-visible 0.3s ease-in-out;
      animation-direction: normal;
      animation-fill-mode: forwards;
      border-radius: $border-radius-main;
      background-origin: border-box;
      background-clip: content-box, border-box;
      border: solid 2px transparent;
      padding: 0;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
      background-image: linear-gradient($color-form, $color-form),
      linear-gradient(101deg, $color-hover, $color-body);
    // }

    &:focus {
      outline: none;
    }
  }

  .list-container {
    display: flex;
    padding-left: 0 !important;
    padding-top: 10px;
    overflow: hidden;
    border-radius: 8px;
    background-color: white;
    // @media screen and (max-width: 575px){
    //   border-radius: 0;
    //   padding-top: 10px;
    //   display: flex;
    //   flex-direction: column;
    //   align-items: center;
    //   background-image: linear-gradient(134deg, rgba(0, 215, 175, 0.75), rgba(70, 115, 122, 0.75));
    //   overflow-y: scroll;
    // }

    @media screen and (min-height: 600px){
      justify-content: center;
    }
    
    height: inherit;
    min-height: 310px;
    margin: 0;
  }

  .list-closed {
    // margin-top: 0;
    // max-height: 0;
    // @media screen and (min-width: 576px){
      // transition: all 0.3s ease;
      border-radius: 8px;
    // }
  }
  
  .list-opened {
    // @media screen and (min-width: 576px){
      // transition: all 0.3s ease;
      border-radius: 8px;
    // }
  }

  .range-borders {
    // @media screen and (max-width: 575px){
    //   .date-inner-container:nth-of-type(1) {
    //     border-bottom-right-radius: 0;
    //     border-bottom-left-radius: 0;
    //   }

    //   .date-inner-container:nth-of-type(2) {
    //     border-top-right-radius: 0;
    //     border-top-left-radius: 0;
    //     margin-bottom: 30px;
    //   }
    // }
  }

  .selected-container {
    align-items: center;
  }

  .arrow-range {
    transition: transform 0.3s cubic-bezier(.48,.36,.26,1.44);
  }

  .arrow-up {
    position: absolute;
    margin-top: -10px !important;
    margin-left: 50px !important;
    z-index: -1;

    // @media screen and (max-width: 575px){
    //   display: none;
    // }
  }

  .arrow-down {
    position: absolute;
    margin-left: 50px !important;
    z-index: -1;
    bottom: -9px;
    // transform: rotate(180deg);

    // @media screen and (max-width: 575px){
    //   display: none;
    // }
  }

  .date-img {
    padding-right: 10px;
    padding-bottom: 8px;
  }

  .date-img-in {
    width: 32px;
    height: 32px;
  }

  .date-img-out {
    width: 38px;
    height: 38px;
  }

  &.dateinner-opened {
    animation: animate-z-visible-v3 0.3s linear;
    animation-direction: normal;
    animation-fill-mode: forwards;
    // @media screen and (max-width: 575px){
    //   width: 100%;
    //   height: 100vh;
    //   top: 0 !important;
    //   left: 0 !important;
    // }
  }
}
