@import '../../../base';

%detail-font {
  font-size: $font-size-smallest;
  font-weight: $font-weight-bold;
  color: $color-input-text;
}

.change-form-container-new {
  display: flex;
  background: #FFFFFF;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  border-radius: 20px;
  padding: 10px 20px 16px 20px;
  margin: 16px 0;

  @media screen and (max-width: 768px) {
    padding: 15px;
    margin: 8px 0;
  }

  .edit-detail-container{
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;

    .edit-detail-inner-container {
      display: flex;
    }

    .edit-detail-label-container,
    .edit-detail-value-container {
      display: flex;
      flex-direction: column;
    }
  }

  .edit-detail-resp-container {
    flex: 1;
    display: flex;
    align-items: center;
    .detail-inner-resp-container {
      flex: 1;
      .edit-detail-value-container {
        display: flex;
        flex-wrap: wrap;
      }

      .detail-resp-value {
        display: inline-block;
        margin-right: 5px;

        @extend %detail-font;
      }
    }

    img {
      height: 24px;
      width: 24px;
      margin-left: 4px;

      transition: transform 0.3s ease;

      &:hover {
        filter: brightness(80%);
        transform: scale(1.1);
      }

      &:active {
        transform: scale(1);
      }
    }
  }
}

.edit-detail-value-container-new {
  margin-bottom: 11px;
  .edit-detail-value {
    margin: 3px 0;
    .detail-logo {
      margin-right: 10px;
      width: 15px;
      height: 15px;
    }

    .detail-value {
      position: relative;
      @extend %detail-font;
      
      .tooltip-button {
        width: 16px;
        height: 16px;
        border: solid 1px $color-hover;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-content: center;
        position: absolute;
        top: 2px;
        right: -25px;
        &:hover {
          cursor: pointer;
        }

        p {
          height: 100%;
          margin-top: 0;
          text-align: center;
          color: $color-hover;
          font-size: 10px;
          font-weight: $font-weight-light;
        }
      }
    }
  }

  .white {
    color: white;
  }
}

.tooltip-text-new {
  text-align: center;
  padding: 10px 0;
  font-size: $font-size-smallest;
  font-weight: $font-weight-semibold;
  color: $color-title;
}