@import '../../../base';

.checkbox-container-v3 {
  margin-bottom: 10px;
  .checkbox-input {
    position: absolute !important;
    left: 0;
    padding-left: 0;
    z-index: 1;
  }

  .custom-control-label {
    &::before, &::after {
      left: 5px !important;
    }
  }
  
  p {
    cursor: pointer;
    margin: 0;
    padding-left: 25px;
    transform: translateY(2px);
  
    font-weight: 700;
    font-size: $font-size-tiny;
    color: $color-input-text;
  }
}
