@import '../../../base';

.selection-container-v3 {
  button {
    width: 100%;
    min-height: 48px;
    border-radius: 12px;

    border: 1px solid $color-line;
    background-origin: border-box;
    background-clip: content-box, border-box;

    color: $color-body;
    font-weight: $font-weight-bold;
    font-size: $font-size-tiny;

    &.selected {
      background-image: linear-gradient($color-form, $color-form), linear-gradient(110deg, $color-hover, $color-hover);
      border: solid 2px transparent;
      color: $color-title;
    }
  }

  .col-sm-6 {
    margin-bottom: 10px;
    @media screen and (min-width: 576px) {
      margin-bottom: 0;
      &:nth-of-type(even) {
        padding-left: 5px;
      }
  
      &:nth-of-type(odd) {
        padding-right: 5px;
      }
    }
  }

  .col-sm-4 {
    margin-bottom: 10px;
    @media screen and (min-width: 576px) {
      margin-bottom: 0;
      &:nth-of-type(3n+0) {
        padding-left: 5px;
      }
      &:nth-of-type(3n+1) {
        padding-left: 5px;
        padding-right: 5px;
      }
      &:nth-of-type(3n+2) {
        padding-right: 5px;
      }
    }
  }
}