/**
 * DatePickerV2.scss
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';
 @import '../forms';

.datepicker-container-v3 {
  button {
    &:active,
    &:hover {
      transform: none;
    }
  }

  &.no-internal-desc {
    .selected-text {
      input {
        transform: translateY(4px) !important;
      }
    }
  }

  .selected-container {
    display: flex;

    .date-input-container {
      height: $input-height;
      width: 50%;
      display: flex;
      align-items: center;

      .img-container {
        @extend %img-container;
        height: 100%;
        .date-in {
          width: 15px;
          height: 15px;
        }

        .date-out {
          width: 17px;
          height: 17px;
        }
      }

      .selected-text {
        position: relative;
        padding: 0;
        display: flex;
        width: 100%;
        height: 100%;
  
        input {
          transform: none !important;
        }
      }
    }

    .text-style {
      width: 100%;
      input {
        transform: translateY(10px);
      }
    }

    .faded-style {
      input {
        opacity: 0.4;
      }
    }

    .seperator {
      display: block;
      width: 1px;
      height: 32px;
      background-color: $color-input-outline;
    }
  }
}

.date-range-picker-new {
  .range-style {
    display: flex;
    align-items: center;
    input {
      position: relative;
      z-index: -1;
    }
  }
}