@import '../../../base';

.redirect-popup {
  max-width: 768px;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  p {
    padding: 0 10px;
  }

  .upper-container {
    p,
    span {
      font-size: $font-size-regular;
      font-weight: $font-weight-semibold;
      color: $color-title;

      @media screen and (max-width: 576px) {
        font-size: $font-size-small;
      }
    }
  }

  .mid-container {
    margin-top: 10px;
    .selection {
      margin-top: -20px;

      @media screen and (max-width: 576px) {
        margin-top: 0;

        .description {
          font-size: $font-size-smallest;
        }
      }
    }

    .error-string {
      margin-top: 20px;
      font-size: $font-size-regular;
      font-weight: $font-weight-semibold;
      color: $color-error-alt;
    }
  }

  .button-container {
    display: flex;
    margin-top: 27px;
    justify-content: center;

    @media screen and (max-width: 576px) {
      margin-top: 10px;
      flex-direction: column;
    }

    button {
      margin: 15px;
    }
  }
}