@import '../../../../base';

.calender-container {
  width: 100%;
  padding: 0 15px;

  .date-col {
    position: relative;
    width: 14.285714%;
  }

  .day-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2px 0;
    height: 30px;
    flex-wrap: wrap;

    .day-disabled,
    .day {
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      left: 0;
    }

    .day:focus {
      outline: none;
    }

    .day p {
      color: #46737a;
      margin: 0;
      cursor: pointer;
    }

    .day-active {
      background-color: $color-hover;
      border-radius: 50%;
      width: 99%;
    }
    
    // .day:active {
    //   background-color: $color-hover-alt;
    //   border-radius: 50%;
    // }
    
    .day-active p {
      color: white;
    }

    @keyframes focus-animation {
      0% {
        background-color: $color-title;
      }
      50% {
        background-color: $color-hover;
      }
      100% {
        background-color: $color-title;
      }
    }

    .day-focus {
      animation: focus-animation 3s infinite;
    }
  }

  .day-range {
    height: 30px;
    width: 50%;
  }
  
  .end-active,
  .left-active ,
  .right-active {
    background: $color-form;
  }
  
  .end-active {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    position: absolute;
    left: 0;
  }
  
  .blocked {
    color: $color-line !important;
    margin: 0;
  }
}
