@import '../../../base';

.tabview-margin-top {
  margin-top: 20px;
}

.tabview-container-new {
  width: 100%;
  position: relative;
  z-index: 0;

  .tab-container {
    display: flex;
    position: relative;
    width: 100%;
    z-index: 150;

    button {
      border: none;
      padding: 0;

      &:focus {
        outline: none;
      }
    }

    .tab {
      height: 2px;
      background-color: #c8e0e4;

      .tab-inner {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        height: 60px;
        width: 100%;
        margin-top: -60px;

        &:hover {
          cursor: pointer;

          img {
            animation: swing 1s;
          }

          .inactive,
          .active {
            color: $color-title;
          }
        }
        
        &:active {
          .inactive,
          .active {
            font-size: $font-size-small;
          }

          .tab-text-big {
            // @media screen and (max-width: 576px) {
            //   font-size: $font-size-regular * 1.2;
            // }

            font-size: $font-size-regular * 0.9 * 1.2;
          }
        }

        .img-container {
          @media screen and (max-width: 576px) {
            display: none;
          }

          max-height: 25px;
          // margin-right: 10px;
          margin-bottom: 12px;
          display: flex;

          .inactive {
            opacity: 0.6;
          }
        }

        p {
          text-align: center;
          padding: 0 16px;
          &:hover {
            cursor: pointer;
          }
        }

        .tab-text-big {
          font-size: $font-size-regular * 0.9;
          font-weight: $font-weight-bold;
          margin-bottom: 12px;

          // @media screen and (max-width: 576px) {
          //   font-size: $font-size-regular;
          // }
        }
      }
      
      .inactive {
        font-size: $font-size-smallest;
        font-weight: $font-weight-normal;
        color: $color-caption;
      }

      .active {
        font-size: $font-size-smallest;
        font-weight: $font-weight-semibold;
        color: $color-title;
      }
    }

    .tab-padding {
      flex: 1;
    }

    .selected-tab {
      height: 2px;
      background-image: linear-gradient(to right, #00d7af, #46737a);
      position: absolute;
      left: 0;
      transition: margin-left 0.3s ease;
    }

    .tab-big {
      height: 8px;

      @media screen and (max-width: 576px) {
        height: 4px;
      }
    }
  }

  .tab-container-charged {
    padding-top: 64px !important; 
    margin-top: 0 !important;
  }

  .tab-container-padding {
    padding-top: 20px;
  }

  .overview-container-padding {
    padding: 30px 0 0 0;

    @media screen and (max-width: 576px) {
      padding: 0;
    }
  }

  @import './carousel.min.scss';
  .carousel {
    ul {
      list-style-type: disc;
    }
    
    .slide {
      background: transparent;
      text-align: initial;
    }
  
    .slider {
      padding: 0 !important;
      margin: 0 !important;
    }

    img {
      width: auto !important;
      vertical-align: initial !important;
    }
  }
  
  .fadeIn,
  .fadeOut {
    animation-duration: 0.3s;
  }

  .scroll-tabs {
    margin: 16px 0;
    border: 1px solid #CADBEA;
    box-sizing: border-box;
    border-radius: 12px;

    height: 72px;
    width: 100%;
    overflow-x: auto;
    overflow-y: hidden;

    display: flex;

    .scroll-tabs-inner {
      display: flex;
      align-items: center;
      height: calc(100% - 6px);
    }
    
    .tab-inner {
      cursor: pointer;
      width: 143px;
      height: 48px;
      padding: 0 20px;
      border-radius: 50px;
      display: flex;
      justify-content: center;
      align-items: center;

      font-weight: bold;
      line-height: normal;
      font-size: 12px;
      color: #2C3652;
      text-align: center;
      transition: background-color 0.3s ease;
  
      &:hover {
        background-color: #E0EAFE;
      }
    }

    .active {
      background-color: #E0EAFE;
    }
  }
}

