@import '../../../base';

.age-group-margin {
  max-width: 660px;
  margin: auto;
  margin-bottom: 40px;
}

.age-group-container {
  @media screen and (max-width: 575px) {
    margin-bottom: 30px;
  }

  .title-container {
    width: 100%;
    margin-bottom: 40px;

    @media screen and (max-width: 575px) {
      margin-bottom: 20px;
    }

    button {
      p {
        margin: 0;
        font-weight: $font-weight-bold;
        font-size: $font-size-smallest;
        color: $color-input-form-landing-desc2;

        &:hover {
          text-decoration: underline;
        }

        @media screen and (max-width: 575px) {
          font-size: $font-size-tiny;
        }
      }
    }

    h3 {
      font-weight: $font-weight-bold;
      font-size: $font-size-big;
      text-align: center;

      color: $color-hover;
      margin: auto;

      @media screen and (max-width: 575px) {
        font-size: $font-size-smallest;
        margin-bottom: 10px;
      }
    }

    h6 {
      text-align: center;
      font-weight: $font-size-regular;
      font-size: $font-size-smallest;
      color: $color-input-form-landing-desc2;
      white-space: pre-wrap;
      margin-top: 5px;

      @media screen and (max-width: 575px) {
        margin-top: 0;
        transform: translateY(-8px);
        font-size: $font-size-extra-tiny;
      }
    }

    .employees {
      transform: translateY(5px);
      font-weight: $font-weight-bold;
      font-size: $font-size-smallest;
      text-align: right;

      color: $color-input-text;

      @media screen and (max-width: 575px) {
        font-size: $font-size-tiny;
      }
    }
  }
}